import React,{Component} from 'react';
import './product.css';
import StarRating from './tools/starRating.js';



class Product extends Component{
	constructor(props){
		super(props)
		this.state = {display:"none",cartHook:this.props.cartHook,product:{img:1,id:1,name:"",type:"",price:0,stock:false,sizes:[],ratingCount:0,ratingVal:0},qty:1,size:0}
		this.qtyChange = this.qtyChange.bind(this)
		this.sizeChange = this.sizeChange.bind(this)
		this.updateProduct = this.updateProduct.bind(this)

		
		
	}
	
	componentDidMount(){
		
	}
	
	componentWillReceiveProps(nextProps) {
		this.state.cartHook = nextProps.cartHook
		this.setState({cartHook:nextProps.cartHook})
	}
	
	updateProduct(rating) {
		let product = this.state.product
		product.ratingVal = rating.val
		product.ratingCount = rating.count
		this.setState({product:product})
		this.props.updateProduct(product)
	}
	
	



	addNewItem(cartHook,id){
		
		let newList = cartHook.state.items.push({key:cartHook.state.items.length+1,id:this.state.product.id,name:this.state.product.name,price:this.state.product.price,qty:this.state.qty,img:this.state.product.img})
		cartHook.setState({items:cartHook.state.items,visible:"block",empty:false})

	}


	html_getQtyOptions(){
		let num_opt = 10
		var html = new Array(num_opt);

		for (var j = 1; j <= num_opt; j++) {
		html.push(<option value={j}>{j}</option>)
		}
		return(html)
		
		
	}
	
	html_getSizeOptions(){
		let num_opt = this.state.product.sizes.length
		var html = new Array(num_opt);

		for (var j = 0; j < num_opt; j++) {
		html.push(<option value={this.state.product.sizes[j]}>{this.state.product.sizes[j]}</option>)
		}
		return(html)
		
		
	}
	
	displaySizeSel(){
		if(this.state.product.sizes.length > 0){
			return("block")
		}else{
			return("none")
		}
	}
	
	renderIsStock(type){
		if(type == "text"){
			if(this.state.product.stock > 0){
				return("Avaible in stock")
			}else{
				return("Not avaible in stock")
			}
		}
		if(type == "color"){
			if(this.state.product.stock > 0){
				return("green")
			}else{
				return("red")
			}
		}
	}
	
	qtyChange(event){

		this.setState({qty:event.target.value})
	}

	sizeChange(event){

		this.setState({size:event.target.value})
	}
	
	render(){
		return(
			<div className="Item2" style={{display:this.state.display}}>

			
			<div className="item_border">
				<div className="item_data">
					<div className="itemPhoto">
						<img src={require('./img/items/'+this.state.product.img+'.jpg')}></img>
					
					</div>
				<div className="itemInfo">

				<StarRating updateProduct={this.updateProduct} ratingCount={this.state.product.ratingCount} ratingVal={this.state.product.ratingVal}  view="1" clickable={true} itemid={this.state.product.id} />

					<div className="item_title">{this.state.product.name}</div>
					<div className="item_cat">{this.state.product.type}</div>
					<div className="item_price">{this.state.product.price} EUR</div>
					<div className="item_avaiable" style={{color:this.renderIsStock("color")}}>{this.renderIsStock("text")}</div>
					
					<div className="item_qty">Quantity:
					<select value={this.state.qty} onChange={this.qtyChange}>{this.html_getQtyOptions()}</select></div>
					
					
					<div className="item_size" value={this.state.size} onChange={this.sizeChange} style={{display:this.displaySizeSel()}}>Choose Size:
					<select>{this.html_getSizeOptions()}</select></div>

					
					
					
					<button onClick={() => this.addNewItem(this.state.cartHook,1)}>Add to cart</button>
					
				</div>
					<div className="otherpics">
						<img src={require('./img/items/'+this.state.product.img+'.jpg')}></img>
						<img src={require('./img/items/'+this.state.product.img+'.jpg')}></img>
						<img src={require('./img/items/'+this.state.product.img+'.jpg')}></img>
					</div>

					
				</div>
				
				

				
				
			</div>
			</div>
		)
	}
	
}

export default Product