import React, {Component} from 'react';
import { Redirect, Router } from 'react-router-dom';


class SearchTool extends Component{
	constructor(props){
		super(props)
		this.state = {searchval:"Search for product",search:'',categoryHook:false}
		this.changeValue = this.changeValue.bind(this)

	}
	
	componentWillReceiveProps(NextProps){
		if(NextProps.categoryHook != undefined){
			this.setState({categoryHook:NextProps.categoryHook})
		}
		
	}
	
	
	openSearch(event){
		this.setState({search:event.target.value})
		if(event.target.value == "Search for product"){
			this.setState({searchval:""})

		}
		if(event.target.value != "Search for product"){
			this.setState({searchval:"Search for product"})

		}

	}
	
	changeValue(event){
	this.setState({searchval : event.target.value,search:event.target.value})
	}
	
	
	clickSearch(){
		this.props.forceRefresh("Search: "+this.state.search)
		return(1)
		let hrf = window.location.href.split("#")
		let link = hrf[0]+"#/category/search?search="+this.state.search
		window.location.href = link
		window.location.reload();
		
		
	}
	
	clickSearchEnter(e){
		if(e.key == "Enter"){
			this.setState({redirect:true})
			this.clickSearch()
		}
		
	}
	
	renderRedirect(){
		if(this.state.redirect == true) {
			this.setState({redirect:false})
			let url = '/category/search?search='+this.state.search
			return(<Redirect to={url} />)
		}
	}
	
	render(){
		const { history } = this.props
		return(
			<div className="searchtool">
				<span className="searchBar" onKeyDown={(event) => this.clickSearchEnter(event)}>
					<a style={{width:'20px'}} href={process.env.PUBLIC_URL + '/#/category/search?search='+this.state.search} onClick={() => this.clickSearch()}>
						<img style={{cursor:"pointer"}} src={require("../img/search.png")}></img>
					</a>
					
					<input onChange={this.changeValue} onBlur={(e) => this.openSearch(e)} value={this.state.searchval} onFocus={(e) => this.openSearch(e)}></input>
						{this.renderRedirect()}
				</span>
			</div>
		
		
		)
	}
	
	
	
}

export default SearchTool;