import React, {Component} from 'react';
import './starRating.css';




class starRating extends Component{
	
	constructor(props){
		super(props)
		this.state = {ratingCount:this.props.ratingCount,ratingVal:this.props.ratingVal,viewType:this.props.view,stars:0,clickable:this.props.clickable,starDepo:false,itemid:this.props.itemid}

	}
	
	componentWillReceiveProps(nextProps){

		this.setState({ratingCount:nextProps.ratingCount,ratingVal:nextProps.ratingVal,stars:this.getStarRate({count:nextProps.ratingCount,val:nextProps.ratingVal}),itemid:nextProps.itemid})
	}
	
	componentDidMount(){

		this.setState({stars:this.getStarRate({count:this.state.ratingCount,val:this.state.ratingVal})})
		
	}
	
	renderHtmlRatesCount(){
		if(this.state.viewType == 1){
			return(<span className="ratesVal"> ({this.state.ratingCount}) </span>)
		}
		if(this.state.viewType == 2){
			return(<span><br /><span className="ratesVal"> ({this.state.ratingCount}) </span></span>)
		}

	}
	
	
	getStarRate(rating){
		let stars = Math.floor(rating.val / rating.count)
		if(stars > 5) stars = 5
		this.setState({starDepo:stars})
		return(stars)	
		
	}
	
	renderStars(){
		let html = Array.apply(null, Array(5))
		html = html.map((key,index) => {
			
			if(this.state.stars > index){
				if(this.state.clickable == true){
					return(<span value={index+1} class="fa fa-star checked" onMouseEnter={() => this.hoverStar(index)} onMouseLeave={() => this.starsBackup()} style={{cursor:"pointer"}} onClick={() => this.addNewStars(index+1)}></span>)
				}else{
					return(<span value={index+1} class="fa fa-star checked"></span>)
				}
			}else{
				if(this.state.clickable == true){
					return(<span value={index+1} class="fa fa-star" onMouseEnter={() => this.hoverStar(index)} onMouseLeave={() => this.starsBackup()} style={{cursor:"pointer"}} onClick={() => this.addNewStars(index+1)}></span>)
				}else{
					return(<span value={index+1} class="fa fa-star"></span>)
				}
			
			}
		})
		return(html)
	}
	

	
	hoverStar(star){
		let starDepo = this.state.starDepo
		let starsNow = this.state.stars
		if(starDepo == false){
			starDepo = starsNow
		}
		this.setState({starDepo:starDepo,stars:star+1})
	}
	
	starsBackup(){
		this.setState({stars:this.state.starDepo,starDepo:false})
	}
	
	
	addNewStars(stars){

		fetch('http://localhost:3001/customer/addstars',{method:"post",headers:{"Content-Type":"application/json"},body:JSON.stringify({itemid:this.state.itemid,stars:stars})})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {

			let ratingCount = this.state.ratingCount + 1
			let ratingVal = this.state.ratingVal + stars
			this.setState({ratingCount:ratingCount,ratingVal:ratingVal,stars:this.getStarRate({count:this.state.ratingCount,val:this.state.ratingVal})})
			
			this.props.updateProduct({count:this.state.ratingCount,val:this.state.ratingVal});
		})
	}
	
	
	render(){
		return(
			<div className="rating">

				{this.renderStars()}
				{this.renderHtmlRatesCount()}<br />
			</div>
		)
	}
	
	
}

export default starRating