import React,{Component} from 'react';
import './body.css'
import Product from './Product.js';
import StarRating from './tools/starRating.js';

class Body extends Component{
	
	constructor(props){
		super(props)
		this.state = {overlay:"none",currentItem:0,featured:[{img:1,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:2,val:7}},{img:2,id:2,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:3,id:3,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:4,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:5,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:6,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:7,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}},{img:8,id:1,name:"Nike Shoes X",price:200,category:"shoes",sizes:[1,2,3],stock:true,rating:{count:200,val:1632}}]}
		this.refProf = false;
		this.updateProduct = this.updateProduct.bind(this)
	}
	
	loadProducts(){
		fetch('http://localhost:3001/customer/products?type=featured')
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			console.log(res)
			if(res.response == 1){
				
				this.setState({featured:res.products})
			}
		})
		
		
	}
	
	updateProduct(product){
		let products = this.state.featured
		products[this.state.currentItem] = product
		this.setState({featured:products})
	}
	
	
	clickOverlay(){
		this.refProf.setState({display:"none"})
		this.setState({overlay:"none"})
	}
	
	componentDidMount(){
		document.getElementById("root").style.backgroundColor = "#f8f9fa"//f8f9fa
		this.loadProducts()
		

	}
	
	clickProduct(item,index){
		this.setState({overlay:"block"})
		
		let product = {id:item.id,name:item.name,type:item.category,price:item.price,stock:item.stock,sizes:item.sizes,ratingCount:item.ratingCount,ratingVal:item.ratingVal,img:item.img}
		
		this.refProf.setState({display:"block",product:product})
		this.setState({currentItem:index})
	}
	
	
	renderProductsToHtml(list){
		let html = list.map((item,index) => 
			<div className="product" onClick={() => this.clickProduct(item,index)}><span className="ratingRight"><StarRating ratingCount={item.ratingCount} ratingVal={item.ratingVal} view="2"/></span><img src={require("./img/items/"+item.img+".jpg")}></img><div className="productDetail"><div className="prodName">{item.name}</div><div className="prodPrice">{item.price} EUR</div><div className="itemDesc"></div></div></div>
		)
		return(html)
		
		
		
	}
	
	
	render(){
		return(
			<div className="Body">

				<span className="logoimg">
					<img src={require("./img/training2.jpg")}></img>
				
				</span>

			
				<div className="Featured">
					<div className="featuredBox">
					<h1></h1>
					{this.renderProductsToHtml(this.state.featured)}
					</div>
				</div>
				
				<div className="Categories">
					<h1>CHOOSE SOMETHING SPECIAL</h1><br /><br />
						<div className="category">
							Shoes
							
							<a href={process.env.PUBLIC_URL + '/#/category/Shoes'}><img src={require("./img/catShoes.jpg")}></img></a>
						</div>
						<div className="category">
							T-Shirts
							<a href={process.env.PUBLIC_URL + '/#/category/Tshirts'}><img src={require("./img/catTshirt.jpg")}></img></a>
						</div>
						<div className="category">
							Pants
							<a href={process.env.PUBLIC_URL + '/#/category/Pants'}><img src={require("./img/catPants.jpg")}></img></a>
						</div>
				</div>
				
				
				<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
			  <Product cartHook={this.props.cartHook} ref={(cd) => this.refProf = cd} updateProduct={this.updateProduct}/>
			  
			</div>
		)
		
	}
}

export default Body;
