import React, {Component} from 'react'
import './cart.css'




class Cart extends Component{
	constructor(props){
		super(props)
		this.state = {visible:"none",empty:true,items:[]}

			
	}
	
	componentDidMount(){
	}


	totalPrice(){
		let current = 0
		for (var j = 0; j < this.state.items.length; j++) {
			current += this.state.items[j].price
			
		}
		
		return(current)
	}
		
	clickCart(){

		if(this.state.visible == "block")
		{
			this.setState({visible:"none"})
		}else{
			this.setState({visible:"block"})
		}
	}
	
	addNewItem(id){
		let newList = this.state.items.push({id:1,name:"Nike Shoes X",price:200,qty:1})
	}
	
	
	renderProductsHTML(){
		if(this.state.items.length > 0){
		let list = this.state.items
		let html = list.map((item,key) => 
			<div className="cartProduct" key="0"><img src={require('../img/items/'+item.img+'.jpg')}></img><div className="cartItemName">{item.name}</div><div className="closeBtn2"><img src={require('../img/x.png')} onClick={() => this.delItem(item.key)}></img></div><div className="cartItemPrice">{item.price*item.qty} EUR</div><div className="itemqty">Quanity:{item.qty}</div></div>
		)	
		return(html)
		}
	}
	
	renderDetailData(){
		
	}
	
	delItem(id){
		let newList = this.state.items.filter(function(item,index){
			return(item.key != id)
		})
		
		
		let empty = this.state.empty;
		if(newList.length == 0){
			empty = true;
		}
		this.setState({items:newList,empty:empty})
	}
	
	ifEmpty(state){
		if(state == 0){
			if(this.state.empty == false){
				return('inline-block')
			}else{
				return('none')
			}
		}
		if(state == 1){
			if(this.state.empty == true){
				return('inline-block')
			}else{
				return('none')
			}
		}
	}

	
	render(){
		

		
		return(
			<div className="Cart" style={{display:this.state.visible}}>
			
				<div className="Title">Your shopping cart<span className="closeBtn"><img src={require('../img/x.png')} onClick={() => this.clickCart()}></img></span></div>
				<div className="blank"></div>
				
				<div className="wrapItems">
				{this.renderProductsHTML()}
				</div>


			<div className="emptyMessage" style={{display:this.ifEmpty(1)}}>Your shopping cart is currently empty</div>
			<div className="total" style={{display:this.ifEmpty(0)}}>Total price: {this.totalPrice()} EUR</div>
			<div className="checkout" style={{display:this.ifEmpty(0)}}>
			
			Pay Now
			</div>
			
			</div>
		
		)
	}
}


export default Cart;


