import React, {Component} from 'react';
import Product_manager from './product_manager.js';
import './cpanel.css'



class Cpanel extends Component{
	constructor(props){
		super(props)
		this.state ={tab:1}
	}
	
	loadCurrentTab(){
		if(this.state.tab == 1){
			return(<Product_manager />)
		}
	}
	
	clickTab(id){
		this.setState({tab:id})
		
	}
	
	
	render(){
		return(	
			<div className="cpanel">
				<div className="leftNavi">
					<div className="cpanel_btn">
						
						<img onClick={() => this.clickTab(1)} src={require('../img/cpanel/products.png')}></img>
						Manage products
					</div>
					<div className="cpanel_btn">
						
						<img onClick={() => this.clickTab(2)} src={require('../img/cpanel/orders.png')}></img>
						Orders
					</div>
					<div className="cpanel_btn">
						
						<img onClick={() => this.clickTab(3)} src={require('../img/cpanel/settings.png')}></img>
						Settings
					</div>
					<div className="cpanel_btn">
						
						<img onClick={() => this.clickTab(4)} src={require('../img/cpanel/msgs.png')}></img>
						Messages
					</div>
					

					
				</div>
					
				<div className="panel">
					{this.loadCurrentTab()}
					
				</div>
					
			
			</div>
		
		)
	}
	
	
}

export default Cpanel;