import React, {Component} from 'react';
import './App.css';
import Body from './Body.js'
import SearchTool from './tools/searchtool.js'
import Category from './Category.js';
import Product from './Product.js';
import Cart from './tools/cart.js';
import Cpanel from './admin/cpanel.js';

import {BrowserRouter as Router,Switch,Route,Link} from "react-router-dom";
import {HashRouter} from "react-router-dom";


class App extends Component {
	
	constructor(props){
		super(props)
		this.child = React.createRef();
		this.state = {child:false,categories:[]}
		this.forceRefresh = this.forceRefresh.bind(this)

	}
	
	
	componentDidMount(){
		this.state.child = this.child
		this.setState({child:this.child})
		
		this.loadCategories()

		

		
	}




	clickCart(){
		if(this.child.state.visible == "block"){
			this.child.setState({visible:"none"})
		}else{
			this.child.setState({visible:"block"})
		}
		
		
	}

	forceRefresh(type){
		this.setState({categoryRefresh:type})
		return 1
		//
		let hrf = window.location.href.split("#")
		let link = hrf[0]+"#/category/"+type+"/"
		window.location.href = link
		window.location.reload();



	}
	
	loadCategories(){
		fetch("http://localhost:3001/customer/categories").then(res => res.json())
		.then(res =>{
			let array = res.map((key) => {
				return(key.name)
			})
			this.setState({categories:array})
		})
	}
	
	renderHtmlCategories(){
		let html = this.state.categories.map((key,index) => {
		return(<div className="cat" key={index}><a href={process.env.PUBLIC_URL + '/#/category/'+key+'/'} onClick={() => this.forceRefresh(key)}>{key}</a></div>)
		})
		return(html)

	}

	render(){
		
	
	
  return (
    <div className="App">
		
      <header className="App-header">
	  <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
		<span className="logo"><a href="#/home">Shoopping</a></span>
		
		
		
		
		
		
			<span className="cart">
				<img src={require("./img/cart3.png")} onClick={() => this.clickCart()}></img>

			</span>
		<div className="topMenu">
			<span className="navibar">

				<div className="cat"><a href={process.env.PUBLIC_URL + '/#/home'}>Home</a></div>
				{this.renderHtmlCategories()}

				
			</span>
			
			
			
			<SearchTool ref={cd => (this.searchToolHook = cd)} forceRefresh={this.forceRefresh} />
			<Cart ref={cd => (this.child = cd)} />
			
		
		</div>
		
		
      </header>

	 
	  
	  
		<HashRouter basename={process.env.PUBLIC_URL +"/"}>
			<Switch>
			  <Route exact path="/">
				<Body cartHook={this.child} />
			  </Route>
			  <Route path="/home">
				<Body cartHook={this.child} />
			  </Route>
			  <Route path="/category">
				<Category cartHook={this.child} searchtool={this.searchToolHook} refresh={this.state.categoryRefresh}/>
			  </Route>
			  <Route path="/admin">
				<Cpanel/>
			  </Route>
			</Switch>
		</HashRouter>
	  
	  <div className="footer">
		<div className="footer_a">
			Terms of Service
		</div>
		<div className="footer_a">
			FAQ
		</div>
		<div className="footer_a">
			Rules
		</div>
		<div className="footer_a">
			Contact
		</div>

		
		
		
	  
	  </div>
	  
	  
	  

	 
		
	  
	  
    </div>
  );
	}
}

export default App;
