import React,{Component} from 'react';
import './category.css';
import Product from './Product.js';
import StarRating from './tools/starRating.js';

class Category extends Component{
	constructor(props){
		super(props)
		this.state = {overlay:"none",category:false,products:[],currentItem:0,page:1}
		this.updateProduct = this.updateProduct.bind(this)
	
	}
	
	componentWillReceiveProps(NextProps){
		if(NextProps.refresh != undefined){
			if(this.state.category != NextProps.refresh) this.state.products = []
			this.state.category = NextProps.refresh
			this.setState({category:this.state.category,products:this.state.products})
			this.loadProducts()
			
		}
	}

	
	componentDidMount(){
		let category = window.location.hash.split("/")
		
		if(category.length >= 3){
			if(category[2].includes('search')){
				category[2] = category[2].replace("?search=",": ")
				category[2] = decodeURI(category[2])
			}
			this.state.category = category[2]
			this.setState({category:category[2]})

		}
		
		this.loadProducts()
		
	}
	updateProduct(product){
		let products = this.state.products
		products[this.state.currentItem] = product
		this.setState({products:products})
	}


	clickProduct(item,index){
		this.setState({overlay:"block"})
		
		let product = {id:item.id,name:item.name,type:item.category,price:item.price,stock:item.stock,sizes:item.sizes,ratingCount:item.ratingCount,ratingVal:item.ratingVal,img:item.img}
		
		this.refProf.setState({display:"block",product:product})
		this.setState({currentItem:index})

		
	}
	
	clickOverlay(){
		this.refProf.setState({display:"none"})
		this.setState({overlay:"none"})
		this.setState({currentItem:0})
	}
	
	renderProductsToHtml(list){
			let html = list.map((item,index) => 
				
				<div className="product" onClick={() => this.clickProduct(item,index)}><span className="ratingRight"><StarRating ratingCount={item.ratingCount} ratingVal={item.ratingVal} view="2"/></span><img src={require("./img/items/"+item.img+".jpg")}></img><div className="productDetail"><div className="prodName">{item.name}</div><div className="prodPrice">{item.price} EUR</div></div></div>
			)
			return(html)
		
	
	}
	
	convertToString(obj){
		return(JSON.stringify(obj))
	}
	
	loadProducts(){
		console.log(this.state.category)
		if(this.state.category.includes('Search') || this.state.category.includes('search')){

			let searchval = this.state.category.split(": ")
			fetch('http://localhost:3001/customer/products?type=search&search='+searchval[1]+"&page="+this.state.page)
			.then(res => res.json(),err => console.log('error',err))
			.then(res => {
				if(res.response == 1 && res.products.length > 0){
					this.setState({products:res.products})
				}else{
					let newpage = this.state.page-1
					if(newpage < 1) newpage = 1
					this.setState({page:newpage})
				}
			})
			return(1)
		}

		fetch('http://localhost:3001/customer/getproducts',{method:"post",body:JSON.stringify({category:this.state.category,page:this.state.page}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1 && res.products.length > 0){
				this.setState({products:res.products})
			}else{
				let newpage = this.state.page-1
				if(newpage < 1) newpage = 1
				this.setState({page:newpage})
			}
		})
		
		
	}
	
	clickPage(vector){
		let page = this.state.page
		if(vector == 'prev' && page > 1){
			page -= 1
		}
		else if(vector == 'next'){
			page += 1
		}
		this.state.page = page
		this.setState({page:page})
		this.loadProducts()
		
	}
	
	
	render(){
		return(
			<div className="CategoryTab">
				<div className="logoShoes">
					<img src={require('./img/logoshoes.jpg')}></img>
				</div>
			
				<div className="categoryT" style={{textTransform: 'capitalize'}}>{this.state.category}</div>
				<div className="productlist">
					<br /><br /><br />
					{this.renderProductsToHtml(this.state.products)}
					
					<span className="NavigateL" onClick={() => this.clickPage('prev')}><img src={require("./img/navigateL.png")}></img></span>
					<span className="NavigateR"onClick={() => this.clickPage('next')}><img src={require("./img/navigateR.png")}></img></span>
				</div>
			
			
				<div id="overlay" style={{display:this.state.overlay}} onClick={() => this.clickOverlay()}></div>
			  <Product cartHook={this.props.cartHook} ref={(cd) => this.refProf = cd} updateProduct={this.updateProduct}/>
			
			</div>
		)
		
		
	}
	
	
}

export default Category;