import React, {Component} from 'react';




class product_manager extends Component{
	constructor(props){
		super(props)

		
		this.state = {inputs:["","New_Category","","","","","",""],selects:[0,0,0],db:{products:[],categories:[]}}
		this.selectChange = this.selectChange.bind(this)
		this.editName = this.editName.bind(this)
		
		this.loadDataBase()
		
		
	}
	
	loadDataBase(){
		fetch("http://localhost:3001/admin/products?type=all").then(res => res.json()).then(
		(result) => {
			this.state.db.products = result.products
			this.setState({db:this.state.db})
		})

		fetch("http://localhost:3001/admin/categories").then(res => res.json()).then(
		(result) => {
			let array = result.map((key) => {
				return(key.name)
			})

			this.state.db.categories = result
			this.setState({db:this.state.db})
		})
	}
	
	deleteProduct(id){

		fetch("http://localhost:3001/admin/removeproduct",{method:"post",body:JSON.stringify({id:id}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1){
				this.loadDataBase()
			}
		})
		

	}
	
	componentDidMount(){
		let inputs = this.state.inputs
		inputs[0] = this.state.db.categories[this.state.selects[0]]
		this.setState({inputs:inputs})
	}
	
	
	selectChange(event,id){
		this.state.selects[id] = event.target.value
		if(id == 0){
		this.state.inputs[id] = this.state.db.categories[event.target.value].name
		}
		this.setState({selects:this.state.selects,inputs:this.state.inputs})
		
	}
	
	inputChange(id,event){
		this.state.inputs[id] = event.target.value
		this.setState({inputs:this.state.inputs})
	}
	
	html_getSelectOptions(type){
		let num_opt = this.state.db.categories.length
		var html = new Array(num_opt);

		for (var j = 0; j < num_opt; j++) {
		html.push(<option value={j} key={j}>{this.state.db.categories[j].name}</option>)
		}
		return(html)
		

	}
	
	
	editName(){
		let currentSelect = this.state.db.categories[this.state.selects[0]].id

		let name = this.state.inputs[0]
		
		fetch("http://localhost:3001/admin/categories",{method:"post",body:JSON.stringify({action:2,name:name,id:currentSelect}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1){
				this.loadDataBase()
			}
		})
		
		
	}
	
	addNewCategory(){
		let name = this.state.inputs[1]
		
		fetch("http://localhost:3001/admin/categories",{method:"post",body:JSON.stringify({action:1,name:name}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1){
				this.loadDataBase()
			}
		})
	}

	deleteCategory(){

		fetch("http://localhost:3001/admin/categories",{method:"post",body:JSON.stringify({action:3,id:this.state.db.categories[this.state.selects[0]]}),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			if(res.response == 1){
				this.loadDataBase()
			}
		})
			
	}
	
	
	readCategoryFromInt(number){
		for(let i = 0;i<this.state.db.categories.length;i++){
			if(this.state.db.categories[i].id == Number(number)){
				return(this.state.db.categories[i].name)
				break;
			}
		}
		return("None")
	}
	
	
	printTableValues(){
	
	let products = this.state.db.products
	
	if(products.length > 0){
		let html = products.map((key) =>
			<tr key={key.id}><td>{key.id}</td><td>{key.name}</td><td>{key.price}</td><td>{key.stock}</td><td>{this.readCategoryFromInt(key.category)}</td><td>{JSON.stringify(key.sizes)}</td><td>{key.img}</td><td style={{cursor:"pointer"}}><center>X</center></td><td onClick={() => this.deleteProduct(key.id)}style={{cursor:"pointer"}} ><center>X</center></td></tr>
		)
		return(html)
	}
	}
	
	addNewProduct(){
		let db = this.state.db
		let categoryid = this.state.db.categories[this.state.selects[1]].id
		
		let itemData = {name:this.state.inputs[2],stock:this.state.inputs[3],price:this.state.inputs[4],category:categoryid,sizes:this.state.inputs[6],imgid:this.state.inputs[7]}
		fetch("http://localhost:3001/admin/products",{method:'post',body:JSON.stringify(itemData),headers:{"Content-Type":"application/json"}})
		.then(res => res.json(),err => console.log('error',err))
		.then(res => {
			console.log(res)
			if(res.response == 1){
				this.loadDataBase()
			}
		})
		

	}
	


	
	render(){
		return(	
			<div className="product_manager">
					<div className="products_manager">
						<div className="title_panel">
							Manage your products list
							
						</div>
						
						<div className="option1">
						
						Edit categories<br />
						<select value={this.state.selects[0]} onChange={(e) => this.selectChange(e,0)}>{this.html_getSelectOptions('category')}</select>
						<input value={this.state.inputs[0]} onChange={(e) => this.inputChange(0,e)}></input><button onClick={() => this.editName()}>Save</button>
						
						
						<br />
						<button onClick={() => this.deleteCategory()}>Delete</button>
						
						</div>
						
						<div className="option2">
						
						Add new category<br />
						<input value={this.state.inputs[1]} onChange={(e) => this.inputChange(1,e)}></input><button onClick={() => this.addNewCategory()}>Save</button>


						
						</div>
						
						<div className="option3">
						
						Product list<br />
						<span className="t1">Add new item</span><br />
						<div className="option3_input_tab">
							Product Name
							<input value={this.state.inputs[2]} onChange={(e) => this.inputChange(2,e)}></input>
						</div>
						
						<div className="option3_input_tab">
							Stock
							<input value={this.state.inputs[3]} onChange={(e) => this.inputChange(3,e)}></input>
						</div>
						
						<div className="option3_input_tab">
							Price
							<input value={this.state.inputs[4]} onChange={(e) => this.inputChange(4,e)}></input>
						</div>
						
						<div className="option3_input_tab">
							Sizes
							<input value={this.state.inputs[6]} onChange={(e) => this.inputChange(6,e)}></input>
						</div>
						
						<div className="option3_input_tab">
							Image
							<input value={this.state.inputs[7]} onChange={(e) => this.inputChange(7,e)}></input>
						</div>
						
						<select value={this.state.selects[1]} onChange={(e) => this.selectChange(e,1)}>{this.html_getSelectOptions('category')}</select>
						
						
						<button onClick={() => this.addNewProduct()}>Add</button>

						
						<br /><br />
						<table>
							<tbody>
						  <tr>
							<th width="30px">ID</th>
							<th width="150px">Name</th>
							<th width="50px">Price</th>
							<th width="30px">Stock</th>
							<th width="100px">Category</th>
							<th width="200px">Sizes</th>
							<th width="100px">Image</th>
							<th width="30px">Save</th>
							<th width="30px">Delete</th>
						  </tr>
						  {this.printTableValues()}
						  
						  </tbody>
						</table>

						
						</div>
						
						
						
					</div>
			
			</div>
		
		)
	}
	
	
}

export default product_manager;